import React, { useEffect, useState } from 'react';
import { HistoryTable } from '../../component';

const History = () => {
  const tableHeader = [{
    name: 'time',
    status: false,
  }, {
    name: 'user',
    status: true,
  }, {
    name: 'action',
    status: true,
  }, {
    name: 'gpio',
    status: true,
  }, {
    name: 'status',
    status: true,
  }, {
    name: 'deviceName',
    status: true
  }]
  const urlApi = 'https://apimanage1306.000webhostapp.com/getDeviceHistory.php'
  
  return (
    <div className='p-4  gap-4'>
      <HistoryTable tableHeader={tableHeader} urlApi={urlApi} />
    </div >
  );
};

export default History;